.App {
  background-color: #000;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  padding: 20px;
}

.content {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 1200px;
}

.logo-image {
  width: 692px;
  height: 161px;
  object-fit: contain;
}
.subtitle {
  color: white;
  font-family: 'Paperlogy4';
  font-size: 40px;
  font-weight: 300; /* Light */
  margin: 20px 0 40px 0;
}
.countdown {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  width: 100%;
}

.day {
  font-family: 'Paperlogy';
  font-size: 121px;
  font-weight: 700;
  color: white;
  display: inline-block;
}

.time-container {
  display: flex;
  flex-direction: column;
  margin-bottom: 24px;
}

.time-top {
  display: flex;
  align-items: center;
}

.hour {
  font-family: 'Paperlogy';
  font-size: 42px;
  font-weight: 700;
  color: white;
}

.ampm {
  margin-top: 20px;
  font-family: 'Paperlogy';
  font-size: 20px;
  font-weight: 700;
  color: white;
  margin-left: 5px;
}



.time-small {
  font-family: 'Paperlogy';
  font-size: 30px;
  font-weight: 700;
  color: #00DBA2;
}

@media screen and (max-width: 767px) {
  .logo-image {
    width: 90%;
    height: auto;
    max-width: 400px;
  }

  .subtitle {
    font-family: 'Paperlogy4';
    font-size: 18px;
    margin: 15px 0 30px 0;
  }

  .day {
    font-size: 60px;
  }

  .hour {
    margin-top: 10px;
    font-size: 24px;
  }

  .ampm {
    font-size: 14px;
  }

  .time-small {
    font-size: 20px;
  }

  .time-container {
    flex-direction: row;
    align-items: center;
    margin-bottom: 0;

  }

  .countdown {
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 10px;
  }

  .minutes-seconds {
    margin-left: 5px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .logo-image {
    width: 80%;
    height: auto;
    max-width: 550px;
  }

  .subtitle {
    font-family: 'Paperlogy4';
    font-size: 32px;
    margin: 18px 0 35px 0;
  }

  .day {
    font-size: 90px;
  }

  .hour {
    font-size: 36px;
  }

  .ampm {
    font-size: 18px;
    margin-top: 18px;
  }

  .time-small {
    font-size: 26px;
  }

  .time-container {
    margin-bottom: 20px;
  }
}

@media screen and (min-width: 1024px) {
  .logo-image {
    width: 692px;
    height: 161px;
    object-fit: contain;
  }

  .subtitle {
    font-family: 'Paperlogy4';
    font-size: 40px;
    font-weight: 300;
    margin: 20px 0 40px 0;
  }

  .day {
    font-family: 'Paperlogy';
    font-size: 121px;
    font-weight: 700;
  }

  .hour {
    font-family: 'Paperlogy';
    font-size: 42px;
    font-weight: 700;
  }

  .ampm {
    font-family: 'Paperlogy';
    font-size: 20px;
    font-weight: 700;
    margin-left: 5px;
  }

  .time-small {
    font-family: 'Paperlogy';
    font-size: 30px;
    font-weight: 700;
    color: #00DBA2;
  }

  .time-container {
    margin-bottom: 24px;
  }
}

.time-container {
  display: flex;
  flex-direction: column;
}

.time-top {
  display: flex;
  align-items: center;
}

.day, .hour, .ampm {
  color: white;
}

.time-small {
  color: #00DBA2;
}