@font-face {
    font-family: 'PressioTEST';
    src: url('./assets/PressioTEST-No.35.otf') format('opentype');
    font-weight: 600;
    font-style: normal;
}

@font-face {
    font-family: 'Paperlogy4';
    src: url('./assets/Paperlogy-4Regular.ttf') format('opentype');
    font-weight: 600;
    font-style: normal;
}
@font-face {
  font-family: 'Paperlogy6';
  src: url('./assets/Paperlogy-6SemiBold.ttf') format('opentype');
  font-weight: 600;
  font-style: normal;
}
body {
  margin: 0;
  font-family: 'PressioTEST', 'Paperlogy', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
